.progress-meter__bar {
  height: 8px;
  border-radius: 8px;
  position: relative;
  margin-bottom: 15px; }

.progress-meter__progress {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  z-index: 0;
  width: 0%;
  border-radius: 8px; }

.progress-meter__label {
  position: relative;
  display: block;
  z-index: 100;
  color: black;
  font-family: "Roboto", Helvetica, sans-serif;
  font-size: 16px;
  line-height: 1.06; }

.progress-meter.fait {
  background-color: rgba(63, 227, 181, 0.1);
  color: #50a384; }
  .progress-meter.fait .progress-meter__bar {
    background-color: #2bca9e; }

.progress-meter.en-cours {
  background-color: rgba(151, 229, 253, 0.1);
  color: #0B9FCD; }
  .progress-meter.en-cours .progress-meter__bar {
    background-color: #00BEF9; }

.progress.a-venir {
  color: #444444;
  margin-top: 10px; }

.progress-meter.timeline .progress-meter__bar {
  background-color: #e0e0e0; }

.progress-meter.timeline .progress-meter__progress {
  background-color: #7181ff; }



