.theme {
  margin-bottom: 50px; }

.theme-title {
  font-size: 34px;
  line-height: 38px;
  font-family: "Gillsans", "Roboto", Helvetica, sans-serif;
  font-weight: bold;
  letter-spacing: -1px;
  margin-bottom: 16px; }

.theme-body,
.no-measure,
.intro-description {
  color: #888888;
  font-family: "Roboto", Helvetica, sans-serif; }

.theme-body {
  margin-top: 0;
  margin-bottom: 16px; }

.intro-description span {
  display: inline-block;
  font-size: 14px;
  line-height: 20px; }

.theme-image {
  margin-bottom: 22px;
  border-radius: 3px;
  background-size: cover;
  min-height: 130px;
  background-position: center center;
  background-color: #e6ecf0; }
  @media only screen and (min-width: 420px) {
    .theme-image {
      height: 300px; } }

.theme-image__text {
  position: absolute !important;
  clip: rect(1px 1px 1px 1px);
  /* IE6, IE7 */
  clip: rect(1px, 1px, 1px, 1px);
  padding: 0 !important;
  border: 0 !important;
  height: 1px !important;
  width: 1px !important;
  overflow: hidden; }



